	module.exports = {"data":{"markdownRemark":{"html":"<h2>Bootstrap Get Started</h2>\n<p>Bootstrap is a free and open source framework developed by Twitter, it provides a variety of templates for use with frontend web development. Using Bootstrap makes it easy to design a fully responsive website and is a framework worth learning.</p>\n<h4>What is a responsive website</h4>\n<p>A responsive website is a website that resizes and rearranges that items on the page depending on the size of your browser. With a responsive website if you resize your browser you can see the changes occur in real time. Bootstrap makes your website responsive for you.</p>\n<h4>How do I add Bootstrap to my page</h4>\n<p>Adding bootstrap to your page is a fast process, just add the following to the <code>&#x3C;head></code> tags in your code.</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-html\"><code><span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;</span>link</span> <span class=\"token attr-name\">rel</span><span class=\"token attr-value\"><span class=\"token punctuation\">=</span><span class=\"token punctuation\">\"</span>stylesheet<span class=\"token punctuation\">\"</span></span> <span class=\"token attr-name\">href</span><span class=\"token attr-value\"><span class=\"token punctuation\">=</span><span class=\"token punctuation\">\"</span>https://maxcdn.bootstrapcdn.com/bootstrap/3.3.7/css/bootstrap.min.css<span class=\"token punctuation\">\"</span></span> <span class=\"token attr-name\">integrity</span><span class=\"token attr-value\"><span class=\"token punctuation\">=</span><span class=\"token punctuation\">\"</span>sha384-BVYiiSIFeK1dGmJRAkycuHAHRg32OmUcww7on3RYdg4Va+PmSTsz/K68vbdEjh4u<span class=\"token punctuation\">\"</span></span> <span class=\"token attr-name\">crossorigin</span><span class=\"token attr-value\"><span class=\"token punctuation\">=</span><span class=\"token punctuation\">\"</span>anonymous<span class=\"token punctuation\">\"</span></span><span class=\"token punctuation\">></span></span>\n</code></pre>\n      </div>\n<p>You will also need to add the following between the <code>body</code> tags in your code. With bootstrap you’ll be using <code>&#x3C;div></code> tags when using many of Bootstrap’s features, each tag will have it’s own unique set of classes applied that allows the tag to perform it’s task. Other sections of this Bootstrap guide will show more examples of how Bootstrap uses <code>&#x3C;div></code> tags. (<code>&#x3C;div></code> tags are not exclusive to Bootstrap however Bootstrap makes use of them.). Below is the code that would would add to the <code>body</code> tags in your code to finish getting started. Keep in mind that while this creates the container, the page will still remain blank until you add content to the container.</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-html\"><code><span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;</span>div</span> <span class=\"token attr-name\">class</span><span class=\"token attr-value\"><span class=\"token punctuation\">=</span><span class=\"token punctuation\">\"</span>alert alert-success<span class=\"token punctuation\">\"</span></span> <span class=\"token attr-name\">role</span><span class=\"token attr-value\"><span class=\"token punctuation\">=</span><span class=\"token punctuation\">\"</span>alert<span class=\"token punctuation\">\"</span></span><span class=\"token punctuation\">></span></span>\n    <span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;</span>strong</span><span class=\"token punctuation\">></span></span>Congratulations!<span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;/</span>strong</span><span class=\"token punctuation\">></span></span>\n    <span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;</span>p</span><span class=\"token punctuation\">></span></span>Bootstrap is now working on this page<span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;/</span>p</span><span class=\"token punctuation\">></span></span>\n<span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;/</span>div</span><span class=\"token punctuation\">></span></span>\n</code></pre>\n      </div>\n<div class=\"alert alert-success\" role=\"alert\">\n    <strong>Congratulations!</strong>\n    <p>Bootstrap is now working on this page</p>\n</div>\n<h4>More Information</h4>\n<ul>\n<li><a href='http://getbootstrap.com/getting-started/' target='_blank' rel='nofollow'>Bootstrap’s official website</a></li>\n</ul>","fields":{"slug":"/bootstrap/bootstrap-get-started/"},"frontmatter":{"title":"Bootstrap Get Started"}}},"pathContext":{"slug":"/bootstrap/bootstrap-get-started/"}}

